<template>
	<view class="inquiry-container">
		<view class="doctor-info">
			<image :src="doctorInfo.avatar" mode=""></image>
			<view>医生姓名：{{doctorInfo.name}}</view>
			<view>医生职称：{{doctorInfo.post}}</view>
			<view>执业编号：{{doctorInfo.code}}</view>
			<view>
				问题描述：
				<textarea  :value="doctorInfo.desc"/>
			</view>
			<view class="pic">
				<view>上传图片：</view>
				<van-uploader :file-list="fileList" max-count="3" @after-read="afterRead"/>
			</view>
			<view class="sub-btn" @click="submitBtn">提交</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				fileList: [],
				doctorInfo:{
					avatar:'/static/smart/avatar01.png',
					name:'胡文',
					post:'院长',
					code:'A01820809274',
					desc:"擅长内外科，参加国际大型手术",
					year:8,
				},
			}
		},
		methods: {
			 afterRead(event) {
			    const  file  = event.detail.file;
					this.fileList.push(file);
			  },
				submitBtn(){
					uni.showToast({
						title:"提交成功",
						icon:'success'
					})
					setTimeout(()=>{
						uni.switchTab({
							url:'/pages/pet/pet'
						})
					},2000)
				}
		}
	}
</script>

<style lang="scss" scoped>
.inquiry-container{
	padding: 20rpx;
	color: #000;
	line-height: 80rpx;
	font-size: 28rpx;
	image{
		border-radius: 10rpx;
		width: 100%;
		height: 400rpx;
	}
	textarea{
		width: 100%;
		height: 150rpx;
		border: 1rpx solid #efefef;
		text-indent: 2em;
		color: #999;
	}
	.sub-btn{
		width: 80%;
		height: 80rpx;
		line-height: 80rpx;
		text-align: center;
		color: #fff;
		background-color: #1890FF;
		border-radius: 10rpx;
		margin: 20rpx auto;
	}
}
</style>
